import axios from 'axios'

export default {
    namespaced: true,
    actions: {
        getVideosInfo({commit}) {
            axios.get('http://localhost:8080/Videos.json').then(
                ({data}) => {
                    commit('setVideosInfo', data)
                })
        }
    },
    mutations: {
        setVideosInfo(state, data) {
            Object.keys(data).forEach(keys => {
                state[keys].length = 0
            })
            Object.keys(data).forEach((key) => {
                state[key].push(...data[key])
            })
            state.CarouselData.push(
                state.VideosCategory1[0],
                state.VideosCategory2[0],
                state.VideosCategory3[0],
                state.VideosCategory4[0],
                state.VideosCategory5[0],
            )
        }
    },
    state: {
        VideosCategory1: [],
        VideosCategory2: [],
        VideosCategory3: [],
        VideosCategory4: [],
        VideosCategory5: [],
        CarouselData: [],
        behindDis: false,
        isLogin: false,
        currWindow: 0 //0为登录，1为注册
    }
}