<template>
    <div class="Carousel">
        <div class="CarouselDetail">
            <div class="leftNav" @click="changeCarousel('pre')"></div>
                <div class="details" v-for="(item,index) of $store.state.Videos.CarouselData" :key="index"
                     v-if="nowCarousel===index">
                    <transition name="CarouselBG" appear>
                        <div class="CarouselBg" :style="`background-image:url('${item['VideoImgUrl']}')`"></div>
                    </transition>
                    <transition name="CarouselInfo" appear>
                        <div class="cr">
                            <p class="title">{{ item['VideoTitle'] }}</p>
                            <p class="author"><span>作品来自</span><span></span><span>{{ item['VideoAuthor'] }}</span></p>
                            <p class="description">
                                {{ item['VideoDescription'] }}
                            </p>
                            <button class="watch">观看</button>
                        </div>
                    </transition>
                </div>
            <div class="rightNav" @click="changeCarousel('next')"></div>
        </div>
        <div class="CarouselBottom">
            <div v-for="(item,index) of $store.state.Videos.CarouselData" :key="index"
                 :class="{'nowCarousel':nowCarousel===index}"
            @click="changeCarousel(index)"
            ></div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Carousel",
        data() {
            return {
                nowCarousel: 0,
            }
        },
        methods: {
            changeCarousel(to) {
                if (to === 'next') {
                    this.nowCarousel = this.nowCarousel === 4 ? 0 : this.nowCarousel + 1
                    return
                }
                if (to === 'pre') {
                    this.nowCarousel = this.nowCarousel === 0 ? 4 : this.nowCarousel - 1
                    return
                }
                this.nowCarousel=to
            }
        },
        mounted() {
            setInterval(()=>{
                this.changeCarousel('next')
            },5000)
        }
    }
</script>

<style scoped>

    .CarouselBG-enter,.CarouselBG-leave-to{
        opacity: .8;
        /*transition: all 11s;*/
    }
    .CarouselBG-enter-to,.CarouselBG-leave{
        opacity: 1;
        /*transition: all 11s;*/
    }

    .CarouselInfo-enter, .CarouselInfo-leave-to {
        opacity: 0;
        transform: translateX(-1%);
        transition: all .5s;
    }

    .CarouselInfo-enter-to, .CarouselInfo-leave {
        opacity: 1;
        transition: all .5s;
    }

    .Carousel {
        width: 100%;
        height: 450px;
        position: relative;
        background-color: rgba(97, 149, 189, 0.1);
        box-sizing: border-box;
        /*margin-top: 50px;*/
        cursor: pointer;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: flex-start;
    }

    .Carousel:hover .CarouselBg {
        transform: scale(1.05);
        transition: all 1s;
    }

    .CarouselDetail {
        width: 100%;
        position: absolute;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .CarouselDetail > span {
        width: 100%;
        height: 100%;
    }

    .leftNav, .rightNav {
        width: 30px;
        height: 30px;
        position: absolute;
        border-top: 2px solid rgba(255, 255, 255, 0.7);
        border-left: 2px solid rgba(255, 255, 255, 0.7);
        z-index: 2;
    }

    .leftNav:hover, .rightNav:hover {
        border-top: 2px solid white;
        border-left: 2px solid white;
    }

    .leftNav {
        transform: rotate(-45deg);
        left: 20px;
    }

    .rightNav {
        transform: rotate(135deg);
        right: 20px;
    }

    .details {
        width: 100%;
        height: 100%;
        display: flex;
        position: absolute;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
    }

    .CarouselBg {
        width: 100%;
        height: 100%;
        background: url("../assets/imgs/2683_2400.png") no-repeat center 25%;
        background-size: 100% auto;
        position: absolute;
        transition: all 1s;
        z-index: 0;
    }

    .CarouselBg::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 1005px;
        background-image: linear-gradient(to right, black 0, transparent 80%, black 100%);
    }

    .cr {
        width: 100%;
        height: 80%;
        z-index: 1;
        position: absolute;
        box-sizing: border-box;
        padding: 30px 100px;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
    }

    .title {
        color: white;
        font-size: 40px;
        font-weight: 700;
        width: 50%;
        margin: 10px 0;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .author {
        color: white;
        margin: 10px 0;
        font-size: 14px;
    }

    .author span:nth-child(1) {
        color: rgba(255, 255, 255, 0.6);
        font-size: 13px;
    }

    .author span:nth-child(2) {
        width: 5px;
        height: 5px;
        margin: 0 10px;
    }

    .author span:nth-child(3):hover {
        color: deepskyblue;
    }

    .description {
        color: rgba(255, 255, 255, 0.8);
        font-size: 17px;
        margin: 10px 0;
        font-weight: 100;
        width: 50%;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .watch {
        position: relative;
        width: 130px;
        height: 40px;
        margin: 10px 0;
        padding-left: 25px;
        border: none;
        outline: none;
        color: white;
        font-size: 18px;
        font-weight: 100;
        border-radius: 10px;
        background-color: rgba(0, 191, 255, 0.75);
        cursor: pointer;
        letter-spacing: 10px;
        text-indent: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .3s;
    }

    .watch:hover {
        background-color: deepskyblue;
        transition: all .3s;
    }

    .watch::before {
        content: '';
        position: absolute;
        left: 25px;
        border-top: 8px solid transparent;
        border-left: 15px solid white;
        border-bottom: 8px solid transparent;
    }

    .CarouselBottom {
        position: absolute;
        width: 100%;
        height: 20px;
        bottom: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .CarouselBottom div {
        width: 10px;
        height: 10px;
        margin: 0 10px;
        border-radius: 100%;
        background-color: rgba(255, 255, 255, 0.5);
    }

    .CarouselBottom div.nowCarousel {
        background-color: rgba(255, 255, 255, 1);
    }
</style>