<template>
    <div>
        <TopBanner/>
        <Carousel/>
        <VideoContent/>
        <FootBanner/>
        <transition name="behind" appear>
            <div id="behind" v-if="$store.state.Videos.behindDis">
                <transition-group name="behind_inner" appear>
                    <div class="loginWindow" v-if="$store.state.Videos.currWindow===0" :key="0">
                        <p>
                            <ruby><span>用户登录 | Pilipili</span>
                                <rt></rt>
                            </ruby>
                        </p>
                        <input
                                type="text"
                                placeholder="在此输入用户名"
                                v-model="login.account"
                                @keydown.prevent="checkInput($event,'login','account')"
                                @paste.prevent
                                @focus="selectAll"
                        >
                        <span class="accountTips" v-text="loginAccountTips">提示</span>
                        <input
                                type="password"
                                placeholder="在此输入用户密码"
                                v-model="login.password"
                                @copy.prevent
                                @keydown.prevent="checkInput($event,'login','password')"
                                @paste.prevent
                        >
                        <span class="passwordTips" v-text="loginPasswordTips">提示</span>
                        <button @click="toLogin">登录</button>
                        <div class="close" @click="$store.state.Videos.behindDis=false"></div>
                        <div class="toRegAndLog" @click="toReg">没有账号，去注册</div>
                    </div>

                    <div class="registryWindow" v-if="$store.state.Videos.currWindow===1" :key="1">
                        <p>
                            <ruby><span>用户注册 | Pilipili</span>
                                <rt></rt>
                            </ruby>
                        </p>
                        <input
                                type="text"
                                placeholder="只能输入纯英文或数字或英文数字混合，特殊符号_-"
                                v-model="registry.account"
                                @keydown.prevent="checkInput($event,'registry','account')"
                                @paste.prevent
                                @focus="selectAll"
                        >
                        <span v-text="registryAccountTips">请输入账号，不少于6位</span>
                        <input
                                type="password"
                                placeholder="请输入登录密码，规则同账号"
                                v-model="registry.password"
                                @copy.prevent
                                @keydown.prevent="checkInput($event,'registry','password')"
                                @paste.prevent
                        >
                        <span v-text="registryPasswordTips">请输入密码，不少于8位</span>
                        <input
                                type="password"
                                placeholder="重复登录密码"
                                v-model="registry.passwordRepeat"
                                @copy.prevent
                                @keydown.prevent="checkInput($event,'registry','passwordRepeat')"
                                @paste.prevent
                        >
                        <span v-text="registryPasswordRepeat">请重复不少于8位的密码</span>
                        <button @click="toRegistry">注册</button>
                        <div class="close" @click="$store.state.Videos.behindDis=false"></div>
                        <div class="toRegAndLog" @click="toLog">已有账号，去登录</div>
                    </div>

                </transition-group>
            </div>
        </transition>
    </div>
</template>

<script>
    import TopBanner from "@/components/TopBanner";
    import Carousel from "@/components/Carousel";
    import VideoContent from "@/components/VideoContent";
    import FootBanner from "@/components/FootBanner";

    export default {
        name: "MainPage",
        data() {
            return {
                login: {
                    account: '',
                    password: ''
                },
                registry: {
                    account: '',
                    password: '',
                    passwordRepeat: ''
                }
            }
        },
        computed: {
            loginAccountTips() {
                return this.login.account.trim() === '' ? '请输入账户' : this.login.account.trim().length >= 6 ? '' : '用户名长度至少6位'
            },
            loginPasswordTips() {
                return this.login.password.trim() === '' ? '请输入密码' : this.login.password.trim().length >= 8 ? '' : '密码长度至少8位'
            },
            registryAccountTips() {
                return this.registry.account.trim().length < 6 ? '请输入长度至少6位的账户，至多15位' : ''
            },
            registryPasswordTips() {
                return this.registry.password.trim().length < 8 ? '请输入长度至少8位的密码，至多20位' : ''
            },
            registryPasswordRepeat() {
                return this.registry.passwordRepeat.trim() === this.registry.password.trim() ? '' : '密码不一致'
            }
        },
        methods: {
            toLogin() {
                if (this.login.account.trim().length >= 6 && this.login.password.trim().length >= 8)
                    this.$store.state.Videos.behindDis = false
            },
            toRegistry() {
                if (this.registry.account.trim().length >= 6 && this.registry.passwordRepeat.trim().length >= 8)
                    this.$store.state.Videos.behindDis = false
            },
            toLog() {
                this.$store.state.Videos.currWindow = 0
            },
            toReg() {
                this.$store.state.Videos.currWindow = 1
            },
            //检查输入，相当于模拟了一个input框
            checkInput(e, type, type2) {
                //获取输入框光标选择的开始和结束位置，两者等同于输入框内容的长度既是没有选择
                let selA = e.target.selectionStart, selB = e.target.selectionEnd
                //把已输入的内容拆成数组
                let tmpArr = this[type][type2].split('')
                //设置可以输入的内容
                const canInputArr = [...'qwertyuiopasdfghjklzxcvbnmQWERTYUIOPASDFGHJKLZXCVBNM0123456789-_']
                //当退格键被按下
                if (e.key === 'Backspace')
                    //删掉选中的内容
                    if (selA !== selB) {
                        //删掉指定位置元素
                        tmpArr.splice(selA, selB - selA)
                        //拼成字符串再赋值回去
                        this[type][type2] = tmpArr.join('')
                        //设置光标位置
                        this.$nextTick(() => {
                            e.target.setSelectionRange(selA, selA)
                        })
                    }
                    //退格光标的前一个内容
                    else if (selA !== 0) {
                        //删掉指定位置元素
                        tmpArr.splice(selA - 1, 1)
                        //拼成字符串再赋值回去
                        this[type][type2] = tmpArr.join('')
                        //设置光标位置
                        this.$nextTick(() => {
                            e.target.setSelectionRange(--selA, selA)
                        })
                    }
                //左箭头按下，向左移动光标
                if (e.key === 'ArrowLeft') {
                    this.$nextTick(() => {
                        e.target.setSelectionRange(selA === 0 ? 0 : --selA, selA)
                    })
                }
                //右箭头按下，向右移动光标
                if (e.key === 'ArrowRight') {
                    this.$nextTick(() => {
                        e.target.setSelectionRange(++selA, selA)
                    })
                }
                //账号输入框长度大于15并且没有选择内容，退出
                if (type2 === 'account' && this[type][type2].length >= 15 && selA === selB) return
                if ((type2 === 'password' || type2 === 'passwordRepeat') && this[type][type2].length >= 20) return
                //当按的键不可以输入，退出
                if (!canInputArr.some(i => e.key === i)) return
                //全选了，直接把内容替换掉
                if (selA === 0 && selB === e.target.value.length && selB !== 0) {
                    this[type][type2] = e.key
                    return
                }
                //有选择内容，替换掉选择的内容为按键
                if (selA !== selB) {
                    tmpArr.splice(selA, selB - selA)
                    tmpArr.splice(selA, 0, e.key)
                    this[type][type2] = tmpArr.join('')
                }
                //在光标后插入内容
                else {
                    tmpArr.splice(selA, 0, e.key)
                    this[type][type2] = tmpArr.join('')
                }
                //设置光标位置，先++是因为输入后的字符串元素多了一个，并且一般的顺序都是从左往右
                this.$nextTick(() => {
                    e.target.setSelectionRange(++selA, selA)
                })
            },
            selectAll(e) {
                e.target.select()
            }
        },
        components: {
            TopBanner, Carousel, VideoContent, FootBanner
        }
    }
</script>

<style scoped>
    .behind_inner-enter, .behind_inner-leave-to {
        opacity: 0;
        transform: translateY(20%);
        transition: all .3s;
    }

    .behind_inner-enter-to, .behind_inner-leave {
        opacity: 1;
        transition: all .3s;
    }

    .behind-enter, .behind-leave-to {
        opacity: 0;
        transition: all .3s;
    }

    .behind-enter-to, .behind-leave {
        opacity: 1;
        transition: all .6s;
    }

    div, span {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    #behind {
        width: 100%;
        height: 100%;
        position: fixed;
        z-index: 5;
        top: 0;
        background-color: rgba(0, 0, 0, 0.3);
        backdrop-filter: blur(5px);
        transition: all .3s;
    }

    .loginWindow, .registryWindow {

        background-color: rgba(255, 255, 255, 0.9);
        box-shadow: 0 0 20px 5px rgba(255, 255, 255, 0.2);
        border-radius: 10px;
        position: absolute;
        overflow: hidden;
        /*border: 2px solid deepskyblue;*/
    }

    .loginWindow {
        width: 500px;
        height: 400px;
    }

    .registryWindow {
        width: 500px;
        height: 500px;
    }

    .loginWindow::before, .registryWindow::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        pointer-events: none;
    }

    .loginWindow::before {
        background-image: linear-gradient(to bottom, rgba(234, 140, 81, 0.2) 0, transparent 65px);
    }

    .registryWindow::before {
        background-image: linear-gradient(to bottom, rgba(0, 191, 255, 0.2) 0, transparent 65px);
    }

    .loginWindow p, .registryWindow p {
        font-size: 1.5rem;
        font-weight: 100;
        letter-spacing: 5px;
        text-indent: 5px;
        margin-bottom: 30px;
    }

    .loginWindow p span, .registryWindow p span {
        display: block;
        color: black;
        margin: 5px auto;
    }

    .loginWindow p rt {
        color: #e35a28;
        font-size: .875rem;
    }

    .loginWindow input, .registryWindow input {
        width: 70%;
        height: 40px;
        margin: 20px auto 0;
        border: none;
        font-size: 1rem;
        letter-spacing: 2px;
        border-bottom: 2px solid rgba(0, 191, 255, 1);
        outline: none;
        background-color: transparent;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        color: black;
        text-align: center;
        transition: all .3s;
    }

    ::placeholder {
        font-size: .875rem;
        letter-spacing: 0;
    }

    .loginWindow input:focus, .registryWindow input:focus {
        /*background-color: rgba(0, 191, 255, 0.1);*/
        border-bottom: 2px solid rgb(250, 147, 41);
        color: deepskyblue;
        transition: all .3s;
    }

    .loginWindow > span, .registryWindow > span {
        max-width: 70%;
        font-size: .8rem;
        margin: 10px auto;
        color: crimson;
    }

    .loginWindow button, .registryWindow button {
        width: 100px;
        height: 35px;
        color: white;
        font-size: 1.1rem;
        font-weight: 100;
        letter-spacing: 5px;
        text-indent: 5px;
        border: none;
        outline: none;
        cursor: pointer;
        margin-top: 30px;
        border-radius: 5px;
        transition: all .3s;
    }

    .loginWindow button {
        background-color: rgba(0, 191, 255, 0.5);
    }

    .registryWindow button {
        background-color: rgba(248, 132, 15, 0.5);
    }

    .loginWindow button:hover {
        background-color: deepskyblue;
        transition: all .3s;
    }

    .registryWindow button:hover {
        background-color: rgba(248, 132, 15, 1);
        transition: all .3s;
    }

    div.toRegAndLog {
        position: absolute;
        right: 20px;
        bottom: 40px;
        font-size: .8rem;
        cursor: pointer;
        color: rgba(51, 159, 196, 0.7);
    }

    div.toRegAndLog:hover {
        color: deepskyblue;
    }


    div.close {
        width: 30px;
        height: 30px;
        border-radius: 100%;
        position: absolute;
        top: 10px;
        right: 10px;
        cursor: pointer;
    }

    div.close::before, div.close::after {
        content: '';
        position: absolute;
        width: 25px;
        height: 1px;
        background-color: black;
        transition: all .3s;
    }

    div.close::before {
        transform: rotate(45deg);
    }

    div.close::after {
        transform: rotate(-45deg);
    }

    div.close:hover::before, div.close:hover::after {
        background-color: crimson;
        transition: all .3s;
    }

    div.close:hover::before {
        transform: rotate(135deg);
    }

    div.close:hover::after {
        transform: rotate(45deg);
    }


</style>